import React from 'react';
import './App.css';
import { Layout, Menu, Breadcrumb } from 'antd';
import { registerMicroApps, start, setDefaultMountApp, runAfterFirstMounted } from 'qiankun';
import { render, genActiveRule } from './render'

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            collapsed: false,
        };
    }
    componentDidMount() {

        render({ appContent: '', loading: true });


        registerMicroApps(
            [
            { name: 'react', entry: '//localhost:7100', render, activeRule: genActiveRule('/react') },
                {name: 'atom-react', entry: '//localhost:8010', render, activeRule: genActiveRule('/atom-react')},
                {name: 'device', entry: '//localhost:8001', render, activeRule: genActiveRule('/device')},
            ],
            );
        setDefaultMountApp('/react');

        start({
            prefetch: true,
            jsSandbox: true,
            singular: true,
            fetch: window.fetch,
        });

        runAfterFirstMounted(() => {
            console.log('[MainApp] first app mounted');
        });
    }


    onCollapse = collapsed => {
        console.log(collapsed);
        this.setState({ collapsed });
    };
    _onClick = (path) =>{
        history.pushState(null, path, path)
    };
    render() {
        // console.log('***********************************')
        return (
            <Layout style={{ minHeight: '100vh' }}>
                <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
                    <div style={{height: 32, background: 'rgba(255, 255, 255, 0.2)', margin: 16,}} />
                    <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
                        <Menu.Item key="1">
                            <a onClick={()=>{this._onClick('react')}}>Option 1</a>
                        </Menu.Item>
                        <Menu.Item key="2"  path={'test1'}>
                            <a onClick={()=>{this._onClick('atom-react')}}>Option 2</a>
                        </Menu.Item>
                        <Menu.Item key="3"  path={'test1'}>
                            <a onClick={()=>{this._onClick('device')}}>Option 3</a>
                        </Menu.Item>
                        <SubMenu
                            key="sub1"
                            title={
                                <span>
                  <span>User</span>
                </span>
                            }
                        >
                            <Menu.Item key="3">Tom</Menu.Item>
                            <Menu.Item key="4">Bill</Menu.Item>
                            <Menu.Item key="5">Alex</Menu.Item>
                        </SubMenu>
                        <SubMenu
                            key="sub2"
                            title={
                                <span>
                  <span>Team</span>
                </span>
                            }
                        >
                            <Menu.Item key="6">Team 1</Menu.Item>
                            <Menu.Item key="8">Team 2</Menu.Item>
                        </SubMenu>
                        <Menu.Item key="9">
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Layout className="site-layout">
                    <Header className="site-layout-background" style={{ padding: 0 }} />
                    <Content style={{ margin: '0 16px' }}>
                        <Breadcrumb style={{ margin: '16px 0' }}>
                            <Breadcrumb.Item>User</Breadcrumb.Item>
                            <Breadcrumb.Item>Bill</Breadcrumb.Item>
                        </Breadcrumb>
                        <div id="container" className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                        </div>
                    </Content>
                    <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
                </Layout>
            </Layout>
        )
    }
}


export default App;
